import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 0
    }
  }

  increment = () => {
    this.setState({ count: this.state.count + 1 });
  }

  decrement = () => {
    this.setState({ count: this.state.count - 1 });
  }



  render() {
    return (
      <div className="App">
        <h1>React Counter</h1>
        <h2>{this.state.count}</h2>
        <button onClick={this.increment} className="counter item1">Increase count +</button>
        <button onClick={this.decrement} className="counter item2">Decrease count -</button>
      </div>
    );
  }
}

export default App;
